import IconButton from "../../components/IconButton";
import {IoMdAdd} from "@react-icons/all-files/io/IoMdAdd"
import SearchInput from "../../components/SearchInput";
import Layout from "../../components/Layout";
import { useEffect, useReducer, useState } from "react";
import Form from "./Form";
import { useDispatch, useSelector } from "react-redux";
import BgLoader from "../../components/BgLoader"
import { get } from "../../application/middlewares";
import toast from "react-hot-toast";
import Table from "../../components/Table/Table"
import Head from "../../components/Table/Head"
import Row from "../../components/Table/Row"
import Cell from "../../components/Table/Cell"
import Body from "../../components/Table/Body"
import {BsImage} from "@react-icons/all-files/bs/BsImage"
import NoContent from "../../components/NoContent"
import { BiEdit } from "@react-icons/all-files/bi/BiEdit";
import {FcSalesPerformance} from "@react-icons/all-files/fc/FcSalesPerformance"
import MyPagination from "../../components/Pagination"

function reducer (state, action){
    console.log(action)
    switch(action.type){
        case "ADD_FORM":
            return {
                ...state,
                visible:true,
                form:"add",
                values:"",
                title:"Форма добавления"
            }
        case "SET_CLOSE_MODAL":
            return {
                ...state, 
                visible:false,
                form:""
            }
        case "SET_DATA":
            return{
                ...state,
                data:action.payload.products,
                count:+action.payload.count,
                loading:false
            }
        case "SET_LOADING":
            return {
                ...state,
                loading:action.payload
            }
        case "UPDATE_FORM":
            return{
                ...state,
                visible:true,
                values:action.payload,
                form:"update",
                title:'Форма обновления'
            }
        case "ADD_PRODUCT":
            return{
                ...state,
                data:[...state.data, action.payload],

            }
        case "SET_UPLOADED_IMAGE":
            return {
                ...state,
                data:state.data.map(item => {
                    if(item.id === action.payload.id){
                        item.destination = action.payload.destination
                    }return item
                }),
                visible:false
            }
        case "SALE_FORM":
            return {
                ...state, 
                form:"sale",
                visible:true,
                title:"Форма добавления скидки",
                values:action.payload
            }
        case "UPDATE_PRODUCT":
            return{
                ...state,
                data:state.data.map(item => {
                    if(item.id === +action.payload.id){
                        item = action.payload;
                    }return item;
                })
            }
        case 'SET_LIMIT':
            return{
                ...state,
                limit:action.payload,
                page:0,
                trigger:!state.trigger,
                loading:true
            }
        case 'SET_PAGE':
            return{
                ...state,
                page:action.payload,
                trigger:!state.trigger,
                loading:true
            }
        case "SET_SEACRH":
            return {
                ...state,
                search:action.payload,
                trigger:!state.trigger,
                page:0,
                loading:true
            }
        case "ADD_SALE":
            return {
                ...state,
                data:state.data.map(item => {
                    if(item.id === +action.payload.product_id){
                        item.discount_id = action.payload.id
                        item.discount_value = action.payload.discount_value
                        item.low_val = action.payload.low
                        item.upper_val = action.payload.upper
                    }return item;
                })
            }
        case "IMPORT_EXCEL":
            return {
                ...state,
                form:"import",
                visible:true,
                title:"Импорт excel файла"
            }
        case "SET_PRODUCER":
            return {
                ...state, 
                producer_id:action.payload,
                trigger:!state.trigger,
                page:0
            }
        case "SET_CATEGORY":
            return {
                ...state, 
                category_id:action.payload,
                trigger:!state.trigger,
                page:0
            }
        default: return state
    }
}

function Products (){

    const [state, setState] = useReducer(reducer, {
        visible:false, form:"", title:"",  data:[], page:0, category_id:"",
        limit:30, count:0, values:"", loading:true, limit:20, search:"", producer_id:""
    })

    const dispatch = useDispatch()
    const token = useSelector(state => state.auth.token)
    const [producers, setProducers] = useState([])
    const [categories, setCategories] = useState([])
    
    useEffect(()=>{
        dispatch(get({
            url:`api/admin/products?page=${state.page}&limit=${state.limit}&search=${state.search}&category_id=${state.category_id}&producer_id=${state.producer_id}`,
            token,
            action: (response) =>{
                if(response.success){
                    setState({type:"SET_DATA", payload:response.data.rows})
                }else{
                    toast.error("Неизвестная ошибка")
                    setState({type:"SET_LOADING", payload:false})
                }  
            }
        }))
    }, [state.trigger]) // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(()=>{
        dispatch(get({
            url:`api/admin/producers`,
            token,
            action: (response) =>{
                if(response.success){
                    setProducers(response.data.rows)
                }else{
                    toast.error("Неизвестная ошибка")
                }
            }
        }))
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(()=>{
        dispatch(get({
            url:`api/admin/categories`,
            token,
            action: (response) =>{
                if(response.success){
                    setCategories(response.data.rows)
                }else{
                    toast.error("Неизвестная ошибка")
                }
            }
        }))
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    return (
        <Layout 
            header = {<Header 
                    handleClick = {()=>setState({type:"ADD_FORM", payload:""})} 
                    handleSearch = {(value) => setState({type:"SET_SEACRH", payload:value})}
                    handleExcel = {()=>setState({type:"IMPORT_EXCEL", payload:""})}
                    producers = {producers}
                    setProducer = {(value) => setState({type:"SET_PRODUCER", payload:value})}
                    setCategory = {(value) => setState({type:"SET_CATEGORY", payload:value})}
                    categories = {categories}
                    category_id = {state.category_id} producer_id = {state.producer_id}
                    />} 
            footer={<MyPagination setPage={(value) => setState({type:'SET_PAGE', payload:value})} limit={+state.limit} count={+state.count} page={state.page} setLimit={(value) => setState({type:'SET_LIMIT', payload:value})}/>}
        >
            <Form
                visible = {state.visible}
                title = {state.title}
                form = {state.form}
                token = {token}
                values = {state.values}
                setCloseModal = {()=>setState({type:"SET_CLOSE_MODAL", payload:""})}
                addedProduct = {(value) => setState({type:"ADD_PRODUCT", payload:value})}
                setUploadedImage = {(value)=>setState({type:"SET_UPLOADED_IMAGE", payload:value})}
                updateProduct = {(value) => setState({type:"UPDATE_PRODUCT", payload:value})}
                addSale = {(value) => setState({type:"ADD_SALE", payload:value})}
            />
            <BgLoader loading = {state.loading}/>
            <div className="w-full h-full px-6 overflow-y-auto pb-20 ">
                <Table>
                    <Head>
                        <Row>
                            <>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    ID
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Имя продукта
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Цена
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Остаток
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Производитель
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Категория
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Фотография  
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Скидка  
                                </div>
                            </Cell>
                            <Cell>
                                <div  className={` px-2 py-4 font-medium flex flex-row justify-start items-center `}>
                                    Действие
                                </div>
                            </Cell>
                            </>
                        </Row>
                    </Head>
                    <Body>
                    {state.data?.length > 0 
                    ?
                        state.data.map(item => (
                            <Row key ={item?.id}>
                                <>
                                <Cell>
                                    <div className = "p-2">
                                        {item?.id}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className = "p-2">
                                        {item?.name}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className = "p-2">
                                        {item?.price}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className = "p-2">
                                        {item?.stock}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className = "p-2">
                                        {item?.producer_name}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className = "p-2">
                                        {item?.category_name}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className="p-2 flex justify-center items-center">
                                        {item?.destination ? <img className="object-contain rounded-md w-28 h-28" src={`${process.env.REACT_APP_FILE_URL}/${item?.destination}-big.webp`} alt="CTYPES" />: <BsImage className="text-4xl text-gray-500 opacity-80"/>}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className = "p-2">
                                        {item?.discount_id ? 
                                        <>
                                            <p>{item.discount_value}</p>
                                            <p>от {item.low_val.substring(0, 10)}</p>
                                            <p>до {item.upper_val.substring(0, 10)}</p>
                                        </>
                                        : `No`}
                                    </div>
                                </Cell>
                                <Cell>
                                    <div className = "p-2 flex flex-row">
                                        <IconButton tooltip = {"Изменить"} handleClick = {()=>setState({type:"UPDATE_FORM", payload:item})} icon = {<BiEdit className = "text-2xl"/>}/>
                                        {!item?.discount_id &&
                                            <IconButton tooltip = {"Скидки"} handleClick = {()=>setState({type:"SALE_FORM", payload:item})} icon = {<FcSalesPerformance className = "text-2xl"/>}/>
                                        }
                                    </div>
                                </Cell>
                            </>
                        </Row>
                        ))
                    :
                        <>
                        <tr>
                            <td colSpan="10" >
                                <div className="flex w-full h-full py-20 justify-center items-center">
                                    <NoContent title="Нет продуктов"/>
                                </div>
                            </td>
                        </tr>
                        </>
                    }
                    </Body>
                </Table>
            </div>
        </Layout>
    )
}

const Header = ({handleClick, handleSearch, handleExcel, producers, categories, setProducer, setCategory, category_id, producer_id}) =>(
    <div className="flex flex-row justify-between items-center">
        <IconButton tooltip = "Добавить продукт" handleClick={handleClick} icon={<IoMdAdd className="text-2xl "/>}/>
        <button type="button"  ripple="light" onClick = {handleExcel} className="flex justify-center items-center w-48 h-10 mx-2 hover:text-white hover:bg-indigo-600 active:bg-white active:text-indigo-600 rounded-md focus:outline-none shadow-lg text-indigo-600 bg-white">
            Импортировать excel
        </button>
        <div className="flex flex-col relative  ">
            <select value = {producer_id} onChange = {(e)=>setProducer(e.target.value)} className = "flex justify-center items-center h-10 mx-2 hover:text-white hover:bg-indigo-600 active:bg-white active:text-indigo-600 rounded-md focus:outline-none shadow-lg text-indigo-600 bg-white cursor-pointer">
                <option key = {0} value = {""} >Производитель</option>
                {producers?.length > 0 &&
                    producers.map(item => (
                        <option key = {item.id} value = {item.id}>{item.name}</option>
                    ))
                }
            </select>
        </div>
        <div className="flex flex-col  relative  ">
            <select value = {category_id} onChange = {(e)=>setCategory(e.target.value)} className = "flex justify-center items-center h-10 mx-2 hover:text-white hover:bg-indigo-600 active:bg-white active:text-indigo-600 rounded-md focus:outline-none shadow-lg text-indigo-600 bg-white cursor-pointer" >
                <option key = {0} value = {""}>Категория</option>
                {categories?.length > 0 &&
                    categories.map(item => (
                        <option key = {item.id} value = {item.id}>{item.name_ru}</option>
                    ))
                }
            </select>
        </div>
        <div className="w-80">
            <SearchInput action={(value) => handleSearch(value)} placeholder="Поиск"/>
        </div>

    </div>
);


export default Products