import  ModalContainer from "../../components/ModalContainer";
import { useForm } from "react-hook-form";
import * as Yup from 'yup';
import { yupResolver } from "@hookform/resolvers/yup";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import {get, post} from "../../application/middlewares/index.js"
import toast from "react-hot-toast";
import BgLoader from "../../components/BgLoader"
import { axiosInstance } from '../../application/api/index'
import IconButton from "../../components/IconButton";
import {BiTrash} from "@react-icons/all-files/bi/BiTrash"
import ExcelUpload from "../../components/ExcelUpload"

function Form ({visible, values, form, token, setCloseModal, title, addedProduct, setUploadedImage, updateProduct, addSale}){
    const [producers, setProducers] = useState([]);
    const [categories, setCategories] = useState([])
    const {register,  handleSubmit, formState: { errors }, watch, reset, setValue} = useForm({
        resolver:yupResolver(schema(form)),
        defaultValues:{...values}
    })

    const dispatch = useDispatch();
    
    const onSubmit = (data) =>{
        console.log(data)
        setLoading(true)
        dispatch(post({
            url:form === "add" ? `api/admin/add-product` : 
                form === "update" ? `api/admin/update-product/${values.id}` : 
                form === "sale" ? `api/admin/add-sale` : "",
            token,
            data: data,
            action: (response) =>{
                if(response.success){
                    // console.log(response.data.rows) 
                    if(form==="add"){
                        addedProduct(response.data.rows);
                    }
                    if(form === "update"){
                        // console.log("I am in if")
                        // console.log(response.data.rows)
                        console.log("hello some update")
                        updateProduct(response.data.rows)
                    }
                    if(form==="sale"){
                        console.log(response.data.rows)
                        addSale(response.data.rows)
                    }
                    if(selectedFile){
                        const id = form === "add" ? response.data.rows.id : values.id

                        const formData = new FormData()
                        const config = {
                            headers: {
                                'Content-Type': 'multipart/form-data',
                                Authorization: `Bearer ${token}`
                            },
                        }
                        formData.append("picture", selectedFile)
                        axiosInstance.post(`api/admin/add-product-image/${id}`, formData, config).then(res =>{
                            console.log(res.data)
                            if(res){
                                setUploadedImage({id, "destination":res.data.rows.destination})
                                setCloseModal();
                                reset({})
                            }
                        }).catch(err=>{
                            toast.error("Не смог загрузить фотографию")
                        })
                        reset({})
                        setSelectedFile(null);
                        setDelImg(false);
                        setImage(null)
                    }else{
                        setCloseModal()
                        reset({})
                        setSelectedFile(null);
                        setDelImg(false);
                        setImage(null)
                    }
                }else{
                    toast.error("Неизвестная ошибка");
                    setCloseModal()
                    reset({});
                    setDelImg(false)
                    setSelectedFile(null);
                    setImage(null)
                }
            }
        }))
        setLoading(false)
        setSelectedFile(null)
    }
    // const [excel, setExcel] = useState()
    // const addedFile = () =>{
        
    // }

    useEffect(()=>{
        if(form==="update"){
            setValue("name_tm", values.name_tm);
            setValue("description_tm", values.description_tm);
            setValue("name_ru", values.name_ru);
            setValue("description_ru", values.description_ru);
            setValue("name_en", values.name_en);
            setValue("description_en", values.description_en);
            setValue("price", values.price);
            setValue("stock", values.stock);
            setValue("category_id", values.category_id);
            setValue("producer_id", values.producer_id);
        }
        if(form === "sale"){
            // console.log("hello")
            setValue("product_id", values.id);
            // console.log(values.id)
            setValue("discount_type_id", 1)
            setValue("min_value", 1)
        }
    }, [form]) // eslint-disable-line react-hooks/exhaustive-deps

    const [selectedFile, setSelectedFile] = useState()
    const [image, setImage] = useState()
    const [loading, setLoading] = useState(false)
    const [del_img, setDelImg] = useState(false)

    const onChangeHandler = (e) =>{
        setSelectedFile(e.target.files[0])
        setImage(URL.createObjectURL(e.target.files[0]))
    }

    
    useEffect(()=>{
        dispatch(get({
            url:`api/admin/producers`,
            token,
            action: (response) =>{
                if(response.success){
                    setProducers(response.data.rows)
                }else{
                    toast.error("Неизвестная ошибка")
                }
            }
        }))
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(()=>{
        dispatch(get({
            url:`api/admin/categories`,
            token,
            action: (response) =>{
                if(response.success){
                    setCategories(response.data.rows)
                }else{
                    toast.error("Неизвестная ошибка")
                }
            }
        }))
    }, []) // eslint-disable-line react-hooks/exhaustive-deps
    return (
        <ModalContainer size = "lg"
            visible = {visible}
            title = {title}
            setCloseModal = {()=>{
                setCloseModal();
                reset({});
                setSelectedFile(null);
                setDelImg(false);
                setImage(null)
            }}
        >
        <form onSubmit = {handleSubmit(onSubmit)}>
            {
                form === "sale"  ? 
                <>
                    {/* <div className="relative w-full mt-2">
                        <label>Минимальное количество</label>
                        <input type="text" {...register("min_value")}
                            autoComplete="false"
                            className={`${errors.min_value ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                            placeholder="Например 1"
                        />
                        <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                            {errors.min_value?.message}
                        </p>
                    </div> */}
                    
                    <div className="relative w-full mt-2">
                        <label>Скидка (в процентах)</label>
                        <input type="text" {...register("discount_value")}
                            autoComplete="false"
                            className={`${errors.discount_value ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                            placeholder="Например 50"
                        />
                        <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                            {errors.discount_value?.message}
                        </p>
                    </div>
                    <div className = "py-3 flex flex-col">
                        <label className = "pl-2 pb-3">Введите начальное число</label>
                        <input {...register("start_date")} type = "text" onFocus = {(e)=>{e.currentTarget.type="date"}} onBlur={(e)=>e.currentTarget.type="text"}  placeholder = "Введите начальное число" className = "ring-indigo-600 px-3 shadow-inner h-12 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2"/>        
                        <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                            {errors.start_date?.message}
                        </p>
                    </div>
                    <div className = "py-3 flex flex-col">
                        <label className = "pl-2 pb-3">Введите конечное число</label>
                        <input {...register("end_date")} type = "text" onFocus = {(e)=>{e.currentTarget.type="date"}} onBlur={(e)=>e.currentTarget.type="text"}  placeholder = "Введите начальное число" className = "ring-indigo-600 px-3 shadow-inner h-12 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2"/>        
                        <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                            {errors.end_date?.message}
                        </p>
                    </div>
                </>
                : form === "add" || form === "update" ?
            <>
                <div className = "flex flex-row">
                    <div className = "w-1/3 px-2">
                        <div className="relative w-full mb-6">
                            <label>Название продукта на туркменском</label>
                            <input type="text" {...register("name_tm")}
                                autoComplete="false" name = "name_tm"
                                className={`${errors.name_tm ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="Max-Deluxe"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.name_tm?.message}
                            </p>
                        </div>
                        <div className="flex flex-col w-full relative mb-6 p-2">
                            <label>Описание на туркменском</label>
                            <textarea type="text" {...register("description_tm")} name="description_tm"
                                autoComplete="false"
                                className={`${errors.description_tm ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-24 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="описание на русском"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.description_tm?.message}
                            </p>
                        </div>
                    </div>
                    <div className = "w-1/3 px-2">
                        <div className="relative w-full mb-6">
                            <label>Название продукта на русском</label>
                            <input type="text" {...register("name_ru")}
                                autoComplete="false" name = "name_ru"
                                className={`${errors.name_ru ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="Max-Deluxe"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.name_ru?.message}
                            </p>
                        </div>
                        <div className="flex flex-col w-full relative mb-6 p-2">
                            <label>Описание на русском</label>
                            <textarea type="text" {...register("description_ru")} name="description_ru"
                                autoComplete="false"
                                className={`${errors.description_ru ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-24 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="описание на русском"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.description_ru?.message}
                            </p>
                        </div>
                    </div>
                    <div className = "w-1/3 px-2">
                        <div className="relative w-full mb-6">
                            <label>Название продукта на английском</label>
                            <input type="text" {...register("name_en")}
                                autoComplete="false" name = "name_en"
                                className={`${errors.name_en ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="Max-Deluxe"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.name_en?.message}
                            </p>
                        </div>
                        <div className="flex flex-col w-full relative mb-6 p-2">
                            <label>Описание на английском</label>
                            <textarea type="text" {...register("description_en")} name="description_en"
                                autoComplete="false"
                                className={`${errors.description_en ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-24 text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                placeholder="описание на русском"
                            />
                            <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                {errors.description_en?.message}
                            </p>
                        </div>
                    </div>
                </div>
                <div className = "flex flex-row">
                    <div className = "w-1/2 ">
                        <div className = "flex flex-col">
                            <div className="flex flex-col w-full relative mb-6 ">
                                <label>Цена</label>
                                    <input type="text" {...register("price")}
                                        autoComplete="false" name = "price"
                                        className={`${errors.price ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                        placeholder="150"
                                    />
                                <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                    {errors.price?.message}
                                </p>
                            </div>
                            <div className="flex flex-col w-full relative mb-6 ">
                                <label>Остаток</label>
                                    <input type="text" {...register("stock")}
                                        autoComplete="false" name = "stock"
                                        className={`${errors.stock ? 'border-2 border-red-300 ring-red-100' : 'ring-indigo-600'} px-3 shadow-inner h-10 w-full text-base bg-gray-50 rounded-md z-20 focus:bg-white focus:outline-none focus:ring-2 `}
                                        placeholder="150"
                                    />
                                <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                    {errors.stock?.message}
                                </p>
                            </div>
                            <div className="flex flex-col w-full relative mb-6 ">
                                <label>выберите производителя</label>
                                <select value = {watch("producer_id")} onChange = {(e)=>setValue("producer_id", e.target.value)} className = "px-3 py-2 placeholder-gray-400 text-gray-700 dark:text-gray-200  bg-white dark:bg-gray-700 rounded text-sm focus:outline-none  border-0 ring-1 ring-gray-300 dark:ring-gray-800 focus:ring-purple-700 dark:focus:ring-2 dark:focus:ring-gray-600 w-full cursor-pointer">
                                    <option key = {0} value = {0} >Производитель</option>
                                    {producers.length > 0 &&
                                        producers.map(item => (
                                            <option key = {item.id} value = {item.id}>{item.name}</option>
                                        ))
                                    }
                                </select>
                                <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                    {errors.producer_id?.message}
                                </p>
                            </div>
                            <div className="flex flex-col w-full relative mb-6 ">
                                <label>выберите категорию</label>
                                <select value = {watch("category_id")} onChange = {(e)=>setValue("category_id", e.target.value)} className = "px-3 py-2 placeholder-gray-400 text-gray-700 dark:text-gray-200  bg-white dark:bg-gray-700 rounded text-sm focus:outline-none  border-0 ring-1 ring-gray-300 dark:ring-gray-800 focus:ring-purple-700 dark:focus:ring-2 dark:focus:ring-gray-600 w-full cursor-pointer" >
                                    <option key = {0} value = {0}>Категория</option>
                                    {categories.length > 0 &&
                                        categories.map(item => (
                                            <option key = {item.id} value = {item.id}>{item.name_ru}</option>
                                        ))
                                    }
                                </select>
                                <p className="absolute bottom-0 left-0 -mb-4 text-xs font-medium text-red-400">
                                    {errors.category_id?.message}
                                </p>
                            </div>
                        </div>
                    </div>
                    {
                        (form === "add" || !values?.destination || del_img) ? 
                        <div className = "w-1/2 flex flex-col justify-center items-center">
                            <div className = "p-2">
                                <label>Выберите файл</label>
                                <input type = "file" name = "picture" className = "px-3 py-5" onChange = {(e)=>onChangeHandler(e)} />
                            </div>
                            <div className = "w-96 h-full">
                            {image ? 
                                <img className = "p-1 px-3 " alt = "Hello world" src = {image}/>
                            :
                            <>
                            </>
                            }
                            </div>
                        </div>
                        : form === "update" ?
                            <div className = "flex flex-row relative justify-center items-center w-96 h-full p-1 px-5">
                                <div className = "absolute right-0 top-0 rounded-full">
                                    <IconButton tooltip = "Удалить" handleClick = {()=>setDelImg(true)} icon={<BiTrash className="text-2xl "/>}/>
                                </div>
                                <img className = "object-contain rounded-md " src={`${process.env.REACT_APP_FILE_URL}/${values?.destination}-big.webp`} alt="CTYPES"/>
                            </div>
                        : <></>
                    }
                </div>
            </>
            : form === "import" ?
            <ExcelUpload
            myDocument = {{id:1}} token = {token}
            url = {`api/admin/import-from-excel`}
            visible = {true}
            setUploadedImage = {(value) => console.log("hello world")}
            />
            :
            <>
            </>
            }
            <div className= "w-full flex justify-center items-center">
                <button type="submit"  disabled = {loading} className="w-48 flex remove-button-bg justify-center items-center px-4 h-10 text-white transform ease-in-out duration-300 hover:scale-110 active:scale-100 font-semibold text-base rounded-full bg-green-500 hover:bg-green-400 active:bg-green-500 focus:outline-none shadow-md">
                   {loading ? 
                   <BgLoader loading = {loading}/>
                       :
                       <>{form === "add" ? "Добавить" :form === "update" ? "Обновить" : form === "sale" ? "Добавить скидку": ""}</>
                   } 
                </button>
            </div>
        </form>
        </ModalContainer>
    )
}

const schema = (form) => Yup.object().shape({
    name_tm: form === "sale" ? Yup.string().nullable(true) : Yup.string().min(5, "Минимум 5 символов").max(50, "максимум 50 символов").required("Обязательно"),
    description_tm: form === "sale" ? Yup.string().nullable(true) : Yup.string().min(10, "Минимум 10 символов").max(50, "максимум 50 символов").required("Обязательно"),
    name_ru: form === "sale" ? Yup.string().nullable(true) : Yup.string().min(5, "Минимум 5 символов").max(50, "максимум 50 символов").required("Обязательно"),
    description_ru: form === "sale" ? Yup.string().nullable(true) : Yup.string().min(10, "Минимум 10 символов").max(50, "максимум 50 символов").required("Обязательно"),
    name_en:  form === "sale" ? Yup.string().nullable(true) : Yup.string().min(5, "Минимум 5 символов").max(50, "максимум 50 символов").required("Обязательно"),
    description_en:  form === "sale" ? Yup.string().nullable(true) : Yup.string().min(10, "Минимум 10 символов").max(50, "максимум 50 символов").required("Обязательно"),
    price: form === "sale" ? Yup.string().nullable(true) : Yup.number().min(0).max(100000, 'максимум 1000000').required("Обязательное поле"),
    stock: form === "sale" ? Yup.string().nullable(true) : Yup.number().min(0).max(100000, 'максимум 1000000').required("Обязательное поле"),
    producer_id: form === "sale" ? Yup.string().nullable(true) : Yup.number().min(1, "Обязательлное поле").required("Обязательлное поле"),
    category_id: form === "sale" ? Yup.string().nullable(true) : Yup.number().min(1, "Обязательлное поле").required("Обязательлное поле"),
    product_id: form === "sale" ? Yup.number().required() : Yup.string().nullable(true)
})

export default Form;